<template>
    <div id="mainDiv">
        <div id="content">
            <div id="code" @click="$router.push('Story')">
                <span > Here is the story </span><br />
                <span class="comments">/**</span><br />
                <span class="space" /><span class="comments">* Our story needs to be remembered,</span><br />
                <span class="space" /><span class="comments">* so I write some code to prove our love.</span><br />
                <span class="space" /><span class="comments">*/</span><br />
                Boy i = <span class="keyword">new</span> Boy(<span class="string">"曹帅"</span>);<br />
                Girl u = <span class="keyword">new</span> Girl(<span class="string">"乔译萱"</span>);<br />
                <span class="comments">// Oct 1, 2020, I told you I love you. </span><br />
                i.love(u);<br />
                <span class="comments">// Luckily, you accepted and became my girlfriend eversince.</span><br />
                u.accepted();<br />
                <span class="comments">// Since then, I miss u every day.</span><br />
                i.miss(u);<br />
                <span class="comments">// And take care of u and our love.</span><br />
                i.takeCareOf(u);<br />
                <span class="comments">// Love stays oceans apart, oceans an easy reach.</span><br />
                <span class="comments">// Even though we are far apart.</span><br />
                <span class="keyword">boolean</span> longDistance = <span class="keyword">true</span>;<br />
                <span class="keyword">while</span> (longDistance) {<br />
                <span class="placeholder" />i.waitFor(u);<br />
                <span class="placeholder" /><span class="comments">// I think it is unavoidable but worth
                    it.</span><br />
                <span class="placeholder" /><span class="comments">// We had fun.</span><br />
                <span class="placeholder" />i.fun(u);<br />
                <span class="placeholder" /><span class="comments">// We argued.</span><br />
                <span class="placeholder" />i.argueWith(u);<br />
                <span class="placeholder" /><span class="comments">// We reconcile.</span><br />
                <span class="placeholder" />i.reconcile(u);<br />
                <span class="placeholder" />longDistance = i.comeTo(u);<br />
                }<br />
                <span class="comments">// The end of the story is a romantic wedding, we will live happily ever
                    after.</span><br />
                i.marry(u);<br />
                i.liveHappilyWith(u);<br />
            </div>
            <div id="loveHeart">
                <canvas id="garden"></canvas>
                <div id="words">
                    <div id="messages">
                        Qiao, We have fallen in love with each other for
                        <div id="elapseClock"></div>
                    </div>
                    <div id="loveu">
                        Love you<br />
                        till the end of the line.<br />
                        <div class="signature">- Hasen</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import * as $ from 'jquery'
import { Garden } from '../assets/js/garden_dev.js'
// import 'lib-flexible'

var $window = $(window), gardenCtx, gardenCanvas, $garden, garden;
var clientWidth = $(window).width();
var clientHeight = $(window).height();

$(window).resize(function () {
    var newWidth = $(window).width();
    var newHeight = $(window).height();
    if (newWidth != clientWidth && newHeight != clientHeight) {
        location.replace(location);
    }
});


export default {
    methods: {
        timeElapse(date) {
            var current = Date();
            var seconds = (Date.parse(current) - Date.parse(date)) / 1000;
            var days = Math.floor(seconds / (3600 * 24));
            seconds = seconds % (3600 * 24);
            var hours = Math.floor(seconds / 3600);
            if (hours < 10) {
                hours = "0" + hours;
            }
            seconds = seconds % 3600;
            var minutes = Math.floor(seconds / 60);
            if (minutes < 10) {
                minutes = "0" + minutes;
            }
            seconds = seconds % 60;
            if (seconds < 10) {
                seconds = "0" + seconds;
            }
            var result = "<span class=\"digit\">" + days + "</span> days <span class=\"digit\">" + hours + "</span> hours <span class=\"digit\">" + minutes + "</span> minutes <span class=\"digit\">" + seconds + "</span> seconds";
            $("#elapseClock").html(result);
        },
        typewriter(interval) {
            var $ele = $('#code'), str = $ele.html(), progress = 0;
            $ele.html('');
            var timer = setInterval(function () {
                var current = str.substr(progress, 1);
                if (current == '<') {
                    progress = str.indexOf('>', progress) + 1;
                } else {
                    progress++;
                }
                $ele.html(str.substring(0, progress) + (progress & 1 ? '_' : ''));
                if (progress >= str.length) {
                    clearInterval(timer);
                }
            }, interval);
        },
        adjustCodePosition() {
            $('#code').css("margin-top", ($("#garden").height() - $("#code").height()) / 2);
        },
        adjustWordsPosition() {
            $('#words').css("position", "absolute");
            $('#words').css("top", $("#garden").position().top + 195);
            $('#words').css("left", $("#garden").position().left + 70);
        },

        showMessages() {
            this.adjustWordsPosition();
            const _this = this
            $('#messages').fadeIn(5000, function () {
                _this.showLoveU();
            });
        },
        showLoveU() {
            $('#loveu').fadeIn(3000);
        },
        startHeartAnimation() {
            var interval = 50;
            var angle = 10;
            var heart = new Array();
            const _this = this
            var animationTimer = setInterval(function () {
                var bloom = _this.getHeartPoint(angle);
                var draw = true;
                for (var i = 0; i < heart.length; i++) {
                    var p = heart[i];
                    var distance = Math.sqrt(Math.pow(p[0] - bloom[0], 2) + Math.pow(p[1] - bloom[1], 2));
                    if (distance < Garden.options.bloomRadius.max * 1.3) {
                        draw = false;
                        break;
                    }
                }
                if (draw) {
                    heart.push(bloom);
                    garden.createRandomBloom(bloom[0], bloom[1]);
                }
                if (angle >= 30) {
                    clearInterval(animationTimer);
                    _this.showMessages();
                } else {
                    angle += 0.2;
                }
            }, interval);
        },
        getHeartPoint(angle) {
            var t = angle / Math.PI;
            var x = 19.5 * (16 * Math.pow(Math.sin(t), 3));
            var y = - 20 * (13 * Math.cos(t) - 5 * Math.cos(2 * t) - 2 * Math.cos(3 * t) - Math.cos(4 * t));
            return new Array($('#loveHeart').width() / 2 + x, $('#loveHeart').height() / 2 - 55 + y);
        }
    },
    mounted() {
        var $loveHeart = $("#loveHeart");
        $garden = $("#garden");
        gardenCanvas = $garden[0];
        gardenCanvas.width = $("#loveHeart").width();
        gardenCanvas.height = $("#loveHeart").height()
        gardenCtx = gardenCanvas.getContext("2d");
        gardenCtx.globalCompositeOperation = "lighter";
        garden = new Garden(gardenCtx, gardenCanvas);

        $("#content").css("width", $loveHeart.width() + $("#code").width());
        $("#content").css("height", Math.max($loveHeart.height(), $("#code").height()));
        $("#content").css("margin-top", Math.max(($window.height() - $("#content").height()) / 2, 10));
        $("#content").css("margin-left", Math.max(($window.width() - $("#content").width()) / 2, 10));

        // renderLoop
        setInterval(function () {
            garden.render();
        }, Garden.options.growSpeed);

        var together = new Date();
        together.setFullYear(2020, 9, 1);
        together.setHours(9);
        together.setMinutes(55);
        together.setSeconds(0);
        together.setMilliseconds(0);
        const _this = this

        setTimeout(function () {
            _this.startHeartAnimation();
        }, 5000);

        this.timeElapse(together);
        setInterval(function () {
            _this.timeElapse(together);
        }, 500);

        this.adjustCodePosition();
        this.typewriter(75);
    }
}
</script>
<style>
#mainDiv {
    width: 100%;
    height: 100%;
}

#loveHeart {
    float: left;
    width: 670px;
    height: 625px
}

#garden {
    width: 100%;
    height: 100%
}

#elapseClock {
    text-align: right;
    font-size: 18px;
    margin-top: 10px;
    margin-bottom: 10px
}

#words {
    font-family: "sans-serif";
    width: 500px;
    font-size: 24px;
    color: #666
}

#messages {
    display: none
}

#elapseClock .digit {
    font-family: "digit";
    font-size: 36px
}

#loveu {
    padding: 5px;
    font-size: 22px;
    margin-top: 80px;
    margin-right: 120px;
    text-align: right;
    display: none
}

#loveu .signature {
    margin-top: 10px;
    font-size: 20px;
    font-style: italic
}

#clickSound {
    display: none
}

#code {
    float: left;
    width: 440px;
    height: 400px;
    color: #333;
    font-family: "Consolas", "Monaco", "Bitstream Vera Sans Mono", "Courier New", "sans-serif";
    font-size: 12px
}

#code .string {
    color: #2a36ff
}

#code .keyword {
    color: #7f0055;
    font-weight: bold
}

#code .placeholder {
    margin-left: 15px
}

#code .space {
    margin-left: 7px
}

#code .comments {
    color: #3f7f5f
}


#errorMsg {
    width: 100%;
    text-align: center;
    font-size: 24px;
    position: absolute;
    top: 100px;
    left: 0
}
</style>